import { createSlice } from "@reduxjs/toolkit"

var initialState = JSON.parse(localStorage.getItem('state')) || {show: false,loggedin: false,iconnav:false,}

export const Modal = createSlice({
	name: 'modal',
	initialState, 
	reducers: {
		toggle: (state) => {
			state.show = !state.show
		},
		login: (state) => {
			state.loggedin = !state.loggedin
		},
		logout: (state) => {
			state.loggedin = !state.loggedin;
			state.show = !state.show;
			localStorage.clear()
		},
		seticon: (state) => {
			state.iconnav = !state.iconnav;
		}
	},
})


export const { toggle, login, logout, seticon } = Modal.actions

export default Modal.reducer