import React from 'react'
import './Dash.css'
import { useSelector } from 'react-redux'
import SIDEBAR from '../Sidebar/SIDEBAR'
import Topmenu from '../TOPMENU/Topmenu'
import { Outlet,} from 'react-router-dom'


function Dash() {
	const iconnav = useSelector((state) => state.modal.iconnav)
	return (
		<div>
			<SIDEBAR />
			<div
				style={
					iconnav ? { marginLeft: '5vw' } : { marginLeft: '15vw' }
				}
			>
				<Topmenu />
				<Outlet/>
			</div>
		</div>
	)
}

export default Dash
