import React,{useState} from 'react'
import Personalinfo from './Personalinfo';
import Signupinfo from './Signupinfo';
import { useDispatch } from "react-redux";
import { toggle} from '../../Store/ModalReducer'
import {details} from "../../Store/LoginReducer"
import "./Form.css"


function Form() {

	const dispatch = useDispatch();
	const [page, setpage] = useState(0);
	const [Values, setValues] = useState({ username: "", password: ""})
	const [errors, seterrors] = useState(false)
	
	const FormTitles = ["Sign In", "Extra Info"];
    const PageDisplay=()=>{
        if (page === 0) {
			return (
				<Signupinfo
					Values={Values}
					setValues={setValues}
				/>
			)
        }
        else {
            return <Personalinfo Values={Values} setValues={setValues} />
						
        }
	}
	function submithandler() {
		if (
			(Values.username !== '') &
			(Values.password !== '')
		) {
			dispatch(details(Values));
			setpage((curr) => curr + 1)
		} else {
			seterrors((prev) => !prev)
		}
		
	}

    return (
			<div className='form'>
				<div className='form-container'>
					<div className='headerr'>
						<div className='header-head'>
							<div>
								<h5>Logo</h5>
							</div>
							<div>
								<h1>{FormTitles[page]}</h1>
							</div>
							<div>
								<h2 onClick={() => dispatch(toggle())}>X</h2>
							</div>
						</div>
					</div>
					<div className='form-body'>{PageDisplay()}</div>

					<div className='footer-form'>
						<div className='footer-div'>
							<p className='forgot-password'>FORGOT YOUR PASSWORD?</p>
							{page === 0 && (
								<button
									className='login-button'
									onClick={() => {
										page === 0
											? submithandler()
											: setpage((curr) => curr + 1)
									}}
								>
									Login
								</button>
							)}
						</div>
					</div>
					<div className='error-message'>
						{errors ? <h6>*Please Fill Correct Details </h6> : ''}
					</div>
				</div>
			</div>
		)
}

export default Form
