import "./Graphs.css";
import {BiNotepad} from "react-icons/bi"
import {
	LineChart,
	Line,
	XAxis,
	YAxis,
	CartesianGrid,
	Tooltip,
	Legend,
	ResponsiveContainer,
} from 'recharts'
const pdata = [
	{
		month: 'jan',
		name: 'stock',
		Value: 0,
		current: 10,
	},
	{
		month: 'Feb',
		name: 'market',
		Value: 6,
		current: 12,
	},
	{
		month: 'Mar',
		name: 'crypto',
		Value: 15,
		current: 10,
	},
	{
		month: 'Apr',
		name: 'rupee',
		Value: 10,
		current: 5,
	},
	{
		month: 'May',
		name: 'Dollar',
		Value: 6,
		current: 4,
	},
	{
		month: 'June',
		name: 'Yen',
		Value: 0,
		current: 8,
	},
]

function Graph() {
	return (
		<div className='graphs'>
			<h3 className='chart-heading'>
				<BiNotepad />
				Transaction Volume
			</h3>
			<ResponsiveContainer width='90%' height='auto' aspect={2}>
				<LineChart
					data={pdata}
					width={500}
					height={300}
					margin={{ top: 10, right: 30, left: 20, bottom: 5 }}
				>
					<CartesianGrid strokeDasharray='3 3' />
					<XAxis interval={'preserveStartEnd'} dataKey="name" />
                    <YAxis interval={"preserveStartEnd"} />
					<Tooltip contentStyle={{ backgroundColor: 'white' }} />
					<Legend />
					<Line dataKey='Value' stroke='red' activeDot={{ r: 8 }} />
					<Line
						dataKey='current'
						stroke='green'
						activeDot={{ r: 8 }}
					/>
				</LineChart>
			</ResponsiveContainer>
		</div>
	)
}

export default Graph
