import Homepage from './Components/Home/Homepage';
import Dashboard from "../src/Components/Dashboard/Dashboard"
import { Routes, Route, Navigate } from 'react-router-dom'
import { useSelector } from "react-redux"
import React, { useEffect } from 'react'
import Latest from './Sample/Latest';
import Content from './Components/Content/Content';


function App() {
	let state=useSelector((state)=>state.modal)
	useEffect(() => {
		localStorage.setItem("state", JSON.stringify(state));
	}, [state]);
  return (
		<Routes>
			<Route path='/' element={<Homepage />} />
			<Route path='*' element={<Homepage />} />
			<Route path='/dashboard/*' element={<Dashboard />}>
				<Route path='*' element={<Navigate to={'home'} />} />
				<Route path='home' element={<Content />} />
				<Route path='latest' element={<Latest />} />
				<Route path='details' element={<h1>From Details </h1>} />
				<Route path='trending' element={<h1>From Trending </h1>} />
				<Route path=':id' element={<h1>ERROR PAGE </h1>} />
			</Route>
		</Routes>
	)
}

export default App;
