import React from 'react'
import "./Topmenu.css"
import { useDispatch } from "react-redux"
import { logout } from "../../Store/ModalReducer"
import {
	BiBell,
	BiMessageAlt,
	BiExit,
	BiUser,
	BiLineChartDown,
} from 'react-icons/bi'

function Topmenu() {
    const dispatch = useDispatch()
    return (
			<div className='topmenu'>
				<h1>COMPANY NAME</h1>
				<div className='topmenu-lefticons'>
					<BiBell />
					<BiMessageAlt />
                    <BiUser />
                    <BiLineChartDown/>
					<BiExit onClick={() => dispatch(logout())} />
				</div>
			</div>
		)
}

export default Topmenu
