import React from 'react'
import { toggle } from '../../Store/ModalReducer'
import { useDispatch, useSelector } from 'react-redux'
import { Navigate } from 'react-router'
import Form from '../Form/Form'
import "./Home.css"


function Homepage() {
	const shows = useSelector((state) => state.modal.show)
	let loggedin=useSelector((state)=>state.modal.loggedin)
	const dispatch=useDispatch()
	return (
		<>
			{loggedin ? (
				<Navigate to='/dashboard' />
			) : (
				<div className='bodyy'>
					<div className='main'>
						<div className='logo'>
							<h1>LOGO</h1>
						</div>
						<div className='Buttons'>
							<p onClick={() => dispatch(toggle())}>Login</p>
								<p>Register</p>
						</div>
					</div>
				</div>
			)}
			{shows && <Form />}
		</>
	)
}

export default Homepage
