import React,{useState} from 'react'
import "./Acc.css";
import { BiChevronDown, BiChevronUp } from 'react-icons/bi'


function Acc(props) {
    const [show, setshow] = useState(true)
    return (
			<div className='table'>
				<div className='table-inner'>
					<div className='table-top'>
						<h3>{props.name}</h3>
						<span
							className='close-icon'
							onClick={() => setshow((prev) => !prev)}
						>
						{show?<BiChevronUp/>:<BiChevronDown />}
						</span>
					</div>
					{show && (
						<div>
							<ul>
								<li>DATA IS HERE</li>
								<li>DATA IS HERE</li>
								<li>DATA IS HERE</li>
								<li>DATA IS HERE</li>
								<li>DATA IS HERE</li>
								<li>DATA IS HERE</li>
								<li>DATA IS HERE</li>
								<li>DATA IS HERE</li>
							</ul>
						</div>
					)}
				</div>
			</div>
		)
}

export default Acc
