import React from 'react'
import "./Personalinfo.css"
import { useNavigate } from 'react-router-dom'
import { useDispatch } from "react-redux"
import {login } from "../../Store/ModalReducer"

function Personalinfo({ Values, setValues }) {
	let navigate = useNavigate()
	let dispatch = useDispatch();
	function loginhandlerr(e) {
		setValues({ ...Values, select: e.target.value })
		dispatch(login())
		navigate('/dashboard', { replace: true })
	}
    return (
		<div className="select-flex">
				<label>Select Any Of the option Below</label>
				<select
					name='Work'
                    value={Values.select}
                    placeholder="Select Any"
					onChange={loginhandlerr}>
                    <option value=""></option>
					<option value='volvo'>Volvo</option>
					<option value='saab'>Saab</option>
					<option value='opel'>Opel</option>
					<option value='audi'>Audi</option>
				</select>
			</div>
		)
}

export default Personalinfo
