import React from 'react'
import "./SIDEBAR.css"
import { NavLink } from 'react-router-dom'
import {  seticon } from '../../Store/ModalReducer'
import {
	BiHome,
	BiInfinite,
	BiCommentDetail,
	BiTrendingUp,
	BiBarChartAlt,
} from 'react-icons/bi'
import { useDispatch, useSelector } from 'react-redux'

function SIDEBAR() {
	const dispatch = useDispatch()
	const iconnav = useSelector((state) => state.modal.iconnav)
    return (
			<div className={iconnav ? 'side' : 'side-bar'}>
				<div className='side-container'>
					<div
						className='sidebar-logo'
						onClick={() => {
							dispatch(seticon())
						}}
					>
						<BiInfinite className='infinity-logo' />
						<h1>LOGO</h1>
						
					</div>

					<NavLink
						to='/dashboard/home'
						className={(NavData) => {
							return NavData.isActive ? 'Navitem-act' : null
						}}
					>
						<div className='Sidebar-links'>
							<BiHome />
							<p>Home</p>
						</div>
					</NavLink>
					<NavLink
						to='/dashboard/details'
						className={(NavData) => {
							return NavData.isActive ? 'Navitem-act' : null
						}}
					>
						<div className='Sidebar-links'>
							<BiCommentDetail />
							<p>Details</p>
						</div>
					</NavLink>
					<NavLink
						to='/dashboard/latest'
						className={(NavData) => {
							return NavData.isActive ? 'Navitem-act' : null
						}}
					>
						<div className='Sidebar-links'>
							<BiBarChartAlt />
							<p>Latest</p>
						</div>
					</NavLink>
					<NavLink
						to='/dashboard/trending'
						className={(NavData) => {
							return NavData.isActive ? 'Navitem-act' : null
						}}
					>
						<div className='Sidebar-links'>
							<BiTrendingUp />
							<p>Trending</p>
						</div>
					</NavLink>
				</div>
			</div>
		)
}

export default SIDEBAR

